// Publications.jsx
import React from 'react';
import PublicationCard from '../components/PublicationCard';
import publicationsData from '../assets/publications.json';

const Publications = () => {
  var journalArticles = publicationsData.filter((publication) => publication.type === 'journal');
  var conferencePresentations = publicationsData.filter((publication) => publication.type === 'conference');
  var mediaHighlights = publicationsData.filter((publication) => publication.type === 'media');
  conferencePresentations = conferencePresentations.sort((a, b) => b.year - a.year);

  return (
    <div style={{ marginTop: '90px' }}>
      <div style={{ paddingTop: '20px', paddingLeft: '20px' }}>
        <h2 className="year-heading" style={{color: '#8FC1DA'}}>Journal Articles</h2>
        {journalArticles.map((publication, index) => (
          <PublicationCard key={index} {...publication} />
        ))}
        <h2 className="year-heading" style={{color: '#FEFFAC'}}>Conference Presentations</h2>
        {conferencePresentations.map((publication, index) => (
          <PublicationCard key={index} {...publication} />
        ))}
        <h2 className="year-heading" style={{color:'rgb(204, 204, 255)'}}>Media Highlights</h2>
        {mediaHighlights.map((publication, index) => (
          <PublicationCard key={index} {...publication} />
        ))}
      </div>
    </div>
  );
};

export default Publications;
